body {
  background-color: #9b59b6;
}

.info {
  font-weight: 900;
  font-size: calc(1rem + 2vmin);
  line-height: calc((1rem + 2vmin) * 0.7);
  padding-top: 0.2rem;
  position: absolute;
  top: 0;
  left: 0;
  text-align: left;
  margin: calc(1rem + 2vmin);
  pointer-events: none;
}

.info .small {
  font-size: 0.6em;
  font-weight: 400;
  margin-top: 1rem;
}

.info.-number {
  letter-spacing: -1px;
  font-weight: 300;
}

.-gyon {
  background-color: #706fd3;
  color: #706fd3;
}

.-mgyon {
  background-color: #d1ccc0;
  color: #474787;
}
.-mgyon .info {
  /*color: #d1ccc0;*/
  color: rgba(255, 255, 255, 0.5);
}
.-mgyon .canvas-shadow {
  background-color: #474787;
}

.-thunder {
  background-color: #84817a;
  color: #000;
}
.-thunder .info {
  /*color: #aaa69d;*/
  color: rgba(255, 255, 255, 0.5);
}
.-thunder .canvas-shadow {
  background-color: #000;
}

.-drone {
  background-color: #f7f1e3;
  color: #cc8e35;
}
.-drone .info {
  /*color: #f7f1e3;*/
  color: rgba(255, 255, 255, 0.5);
}
.-drone .canvas-shadow {
  background-color: #cc8e35;
}

.-expi {
  background-color: #cd6133;
  color: #000;
}
.-expi .info {
  color: rgba(255, 255, 255, 0.5);
}

.swiper-slide {
  display: flex;
}

.canvas-container {
  padding: 0;
  display: block;
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
}

.canvas-wrapper {
}

canvas {
  display: block;
  height: auto;
  margin: 0;
  display: block;
}

.note {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 0;
  width: 100%;
  text-align: center;
  z-index: 10;
  line-height: 19px;
  font-size: 17px;
  font-weight: 600;
}

.note a {
  color: #444;
  /*border-bottom: 2px solid rgba(255, 255, 255, 0.5);*/
}

.note a:hover {
  color: #000;
}

.note.-more {
  bottom: 0;
}

.note.-how {
  top: 0;
}

.canvas-shadow {
  position: absolute;
  height: 3px;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 1;
}
